import { css, cssSelector, cssVariableAsVar, GenerateStyles, style } from '@umetrics/sartorius-ui-base';

export const stepMarkerStyles: GenerateStyles = (tagName) => {
    const { root, child, is } = cssSelector(tagName);

    return [
        style,
        css`
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
            }

            :host {
                display: block;
                width: fit-content;
            }

            ${root} {
                cursor: pointer;
                display: flex;
                flex-grow: 1;
                flex-direction: row;
                width: 100%;
                gap: 6px;
                align-items: center;
            }
            ${child('mark')} {
                display: flex;
                flex-shrink: 0;
                justify-content: center;
                align-items: center;
                width: 32px;
                height: 32px;
                background: ${cssVariableAsVar('color_primary_1')};
                font-size: 16px;
                font-weight: 500;
                color: ${cssVariableAsVar('color_primary_3')};
                transition: background 0.15s;
            }
            ${child('divider')} {
                height: 1px;
                width: 0px;
                margin-right: 6px;
                padding: 0;
                background: ${cssVariableAsVar('color_secondary_8')};
            }
            slot:not([name]) {
                color: ${cssVariableAsVar('color_secondary_8')};
                font-weight: 500;
                text-overflow: ellipsis;
                overflow: hidden;
                width: 100%;
                white-space: nowrap;
            }

            ${is('!hover', 'compact', '!selected')} ${child('slot-wrapper')} {
                max-width: 0px;
                width: auto;
                overflow: hidden;
                transition: max-width 0.1s;
                transition-delay: 0.1s;
            }
            ${is('hover', 'compact', '!selected')} ${child('slot-wrapper')} {
                max-width: 500px;
                width: auto;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                transition: max-width 0.4s;
                transition-delay: 0.1s;
            }

            /* Mark types */
            ${is('completed')} ${child('mark')} {
                background: ${cssVariableAsVar('color_secondary_4')};
            }

            ${is('selected')} ${child('mark')} {
                color: ${cssVariableAsVar('color_primary_2')};
                background: ${cssVariableAsVar('color_secondary_10')};
            }

            ${is('selected')} slot:not([name]) {
                color: ${cssVariableAsVar('color_primary_3')};
            }

            /* Disabled states */

            ${is('disabled')} {
                pointer-events: none;
            }
            ${is('disabled')} ${child('mark')} {
                color: #f59100;
            }
            ${is('disabled')} slot:not([name]) {
                color: ${cssVariableAsVar('color_secondary_4')};
            }
            ${is('disabled', 'completed')} ${child('mark')} {
                color: ${cssVariableAsVar('color_secondary_8')};
            }
            ${is('disabled', 'selected')} ${child('mark')} {
                color: ${cssVariableAsVar('color_secondary_4')};
            }

            /* Hover states */

            ${root}:hover ${child('mark')} {
                transition: background 0.15s;
                background: #ffe133;
            }

            ${is('completed')}:hover ${child('mark')} {
                transition: background 0.15s;
                background: ${cssVariableAsVar('color_secondary_5')};
            }

            ${is('selected')}:hover ${child('mark')} {
                transition: background 0.15s;
                background: ${cssVariableAsVar('color_secondary_9')};
            }
            ${is('!compact')} {
                flex-grow: 1;
            }

            /* Focus states */

            ${child('mark')}:focus-visible {
                outline: 2px dashed ${cssVariableAsVar('color_primary_3')};
                outline-offset: -2px;
            }
        `,
    ];
};
