import { css, cssSelector, GenerateStyles, style } from '@umetrics/sartorius-ui-base';

export const stepControllerStyles: GenerateStyles = (tagName) => {
    const { root } = cssSelector(tagName);

    return [
        style,
        css`
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
            }

            :host {
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                display: block;
                width: 100%;
            }

            ${root} {
                width: 100%;
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: flex-start;
            }
        `,
    ];
};
