import { BaseElement, createClassName, defineElement, html, property, query, state } from '@umetrics/sartorius-ui-base';
import { version } from './metadata';
import { stepControllerStyles } from './stepControllerStyle';
import { StepControllerProperties } from './stepControllerProperties';
import { StepMarker } from './stepMarker';
import { StepMarkerEvent } from './stepMarkerEvent';
import { StepMarkerStates } from './stepMarkerProperties';
import { StepControllerEvent } from './stepControllerEvent';

@defineElement({ tagName: 'step-controller', styles: stepControllerStyles, version })
export class StepController extends BaseElement implements StepControllerProperties {
    // Static members

    // Private members

    // Protected members

    // Public members

    @property({ type: Number, reflect: true, attribute: 'selected-step' })
    get selectedStep() {
        return this._selectedStep;
    }
    set selectedStep(val) {
        if (val === this._selectedStep) return;
        else {
            this._selectedStep = val;
            this._changeSelectedStep(val);
            this.requestUpdate();
        }
    }
    @property({ type: Boolean, reflect: true, attribute: 'controlled' })
    public controlled;

    @state()
    private _steps: any = [];
    @state()
    private _isCompact = false;
    private _stepsWidth = 0;
    private _firstResize = true;
    private _selectedStep;

    @query('div')
    private _root;

    private _resizeObserver: ResizeObserver | null = null;
    private _mutationObserver: MutationObserver | null = null;

    constructor() {
        super();
        this._handleResize = this._handleResize.bind(this);
        this._handleSlotChange = this._handleSlotChange.bind(this);
    }

    connectedCallback() {
        super.connectedCallback();
        this.addEventListener(StepMarkerEvent.StepChange, this._handleStepChange);
    }
    disconnectedCallback() {
        this._uninstallResizeObserver();
        if (this._resizeObserver !== null) {
            this._resizeObserver.disconnect();
        }
        this._mutationObserver?.disconnect();
        this.removeEventListener(StepMarkerEvent.StepChange, this._handleStepChange);
        super.disconnectedCallback();
    }

    protected firstUpdated() {
        this._mutationObserver = new MutationObserver((mutationList) => {
            mutationList.forEach((mutation) => {
                const attr = mutation.attributeName;
                if (attr === 'mywidth') {
                    let stepWidth = 0;
                    this._steps.forEach((step) => {
                        stepWidth += step.getBoundingClientRect().width;
                    });
                    this._stepsWidth = stepWidth;
                }
            });
        });

        this._installResizeObserver();
    }

    protected render() {
        const [, rootClasses] = createClassName(this.tagName.toLowerCase(), {});

        return html`
            <div class="${rootClasses}">
                <slot @slotchange="${this._handleSlotChange}"></slot>
            </div>
        `;
    }

    private _handleSlotChange(e) {
        let hasSelectedStep = false;
        this._steps = [];
        const event = e as CustomEvent;
        const target = event.target as HTMLSlotElement;
        const elems = target.assignedElements();
        elems.forEach((el, i) => {
            const step = el as StepMarker;
            this._mutationObserver!.observe(step, {
                attributeFilter: ['mywidth'],
                attributeOldValue: false,
                subtree: false,
            });
            step.number = i + 1;
            this._steps.push(step);
            if (step.type === 'selected' || this.selectedStep === step.number) {
                hasSelectedStep = true;
                this.selectedStep = step.number;
                step.type = StepMarkerStates.selected;
            }
        });

        if (this._steps.length > 0 && !hasSelectedStep) {
            this._steps[0].type = StepMarkerStates.selected;
        }
    }

    private _handleStepChangeUncontrolled(e) {
        let selectedIndex = 999;
        this._steps.forEach((el, index) => {
            const step = el as StepMarker;
            if (step.number === e.detail.number) {
                step.type = StepMarkerStates.selected;
                if (step !== this._steps[this._steps.length - 1]) {
                    if (
                        step.divider.getBoundingClientRect().width <= 0 &&
                        step !== this._steps[this._steps.length - 1]
                    ) {
                        step.animateDivider(true);
                        step.grow(true);
                    } else {
                        step.style.flexGrow = '1';
                        step.divider.style.width = '100%';
                    }
                }
                selectedIndex = index;
                this._dispatchEvent(StepControllerEvent.StepControllerChange, step);
            } else {
                if (step.type === 'selected' && step.compact && step !== this._steps[this._steps.length - 1]) {
                    step.animateDivider(false);
                    step.grow(false);
                }
                //Skip animation
                step.style.flexGrow = '0';
                step.type = StepMarkerStates.default;
            }
            if (index > selectedIndex && step.compact) {
                this._root.style.width = 100 + '%';
            }
        });
    }

    private _handleStepChangeControlled(e) {
        this._steps.forEach((el) => {
            const step = el as StepMarker;
            if (step.number === e.detail.number) {
                this._dispatchEvent(StepControllerEvent.StepControllerChange, step);
            }
        });
    }

    private _handleStepChange(e) {
        if (this.controlled) {
            this._handleStepChangeControlled(e);
        } else {
            this._handleStepChangeUncontrolled(e);
        }
    }

    private _changeSelectedStep(val) {
        this._steps.forEach((step) => {
            if (step.number === val) {
                step.type = StepMarkerStates.selected;
                // NOTE: Should this be emitted at all since it is not a user interaction?
                if (!this.controlled) {
                    this._dispatchEvent(StepControllerEvent.StepControllerChange, step);
                }
                if (step !== this._steps[this._steps.length - 1] && step.divider.getBoundingClientRect().width <= 0) {
                    step.animateDivider(true);
                    step.grow(true);
                } else {
                    step.style.flexGrow = '1';
                    if (step !== this._steps[this._steps.length - 1]) {
                        step.divider.style.width = '100%';
                    }
                }
            } else {
                if (step.type === 'selected' && step.compact && step !== this._steps[this._steps.length - 1]) {
                    step.animateDivider(false);
                    step.grow(false);
                }
                //Skip animation
                step.style.flexGrow = '0';
                step.type = StepMarkerStates.default;
            }
        });
    }
    private _dispatchEvent(event, item: StepMarker) {
        this.dispatchEvent(
            new CustomEvent(event, {
                detail: { step: item, number: item.number },
                bubbles: true,
                composed: true,
                cancelable: true,
            })
        );
    }

    private _handleResize() {
        if (this._firstResize && this._steps[0] && this._steps[0].getAttribute('myWidth') !== null) {
            this._steps.forEach((el) => {
                this._styleSteps(el);
            });

            this._firstResize = false;
        }
        if (this.clientWidth < this._stepsWidth && !this._isCompact) {
            this._steps.forEach((el) => {
                const step = el as StepMarker;
                step.compact = true;
                this._isCompact = true;
                if (step.type !== 'selected') {
                    step.style.flexGrow = '0';
                }
                this._root.style.width = '100%';
                step.style.flexBasis = 'initial';
            });
        } else if (this.clientWidth > this._stepsWidth && this._isCompact) {
            const marks = this._steps.length;
            this._steps.forEach((step) => {
                step.compact = false;
                this._isCompact = false;
                if (step !== this._steps[this._steps.length - 1]) {
                    step.divider.style.width = '100%';
                    step.style.flexGrow = 1;
                    step.style.flexBasis = 100 / (marks - 1) + '%';
                }
            });
        }
    }

    private _styleSteps(el) {
        if (el === this._steps[this._steps.length - 1]) {
            el.shadowRoot.querySelector('[class$="-divider"]')?.remove();
        } else {
            const step = el as StepMarker;
            step.style.flexBasis = 100 / (this._steps.length - 1) + '%';
            step.divider.style.width = '100%';
        }
    }

    private _uninstallResizeObserver() {
        if (this._resizeObserver !== null && this) {
            this._resizeObserver.unobserve(this);
        }
    }

    private _installResizeObserver() {
        if (this._resizeObserver === null) {
            this._resizeObserver = new ResizeObserver(this._handleResize);
        }
        this._resizeObserver.observe(this);
    }
}
