import {
    BaseElement,
    booleanConverter,
    createClassName,
    defineElement,
    html,
    property,
    query,
} from '@umetrics/sartorius-ui-base';
import { version } from './metadata';
import { stepMarkerStyles } from './stepMarkerStyle';
import { StepMarkerProperties, StepMarkerStates } from './stepMarkerProperties';
import ok from '@umetrics/sartorius-ui-icon/dist/icons/ok';
import { StepMarkerEvent } from './stepMarkerEvent';

@defineElement({ tagName: 'step-marker', styles: stepMarkerStyles, version })
export class StepMarker extends BaseElement implements StepMarkerProperties {
    // Static members
    private static Icon;

    private static animations = {
        flex: {
            expand: [
                {
                    flexGrow: '0.01',
                },
                {
                    flexGrow: '1',
                },
            ],
            retract: [
                {
                    flexGrow: '1',
                },
                {
                    flexGrow: '0.01',
                },
            ],
        },
        divider: {
            expand: [
                {
                    width: '0px',
                },
                {
                    width: '100%',
                },
            ],
            retract: [
                {
                    width: '100%',
                },
                {
                    width: '0px',
                },
            ],
        },
    };
    // Private members

    // Protected members

    // Public members
    @property({ type: Number, reflect: true })
    number;
    @property({ type: Boolean, converter: booleanConverter, reflect: true })
    compact = false;
    @property({ type: Boolean, converter: booleanConverter, reflect: true })
    disabled = false;
    @property({ type: Boolean, converter: booleanConverter, reflect: true })
    completed = false;
    @property({ type: StepMarkerStates, reflect: true })
    type = StepMarkerStates.default;

    /*Visa siffra pÃ¥ hover, ellipsis, hover anim, tester */
    @query('[class$="-divider"]')
    public divider;
    public _type = StepMarkerStates.default;

    private _myWidth = 0;
    private _hoverMouse = false;

    constructor() {
        super();
        this._handleKeydown = this._handleKeydown.bind(this);
    }

    connectedCallback() {
        const imports: any[] = [];
        imports.push({ module: () => import('@umetrics/sartorius-ui-icon'), elem: ['Icon'] });
        StepMarker.dynSubRegister(imports, () => {
            super.connectedCallback();
            this.addEventListener('keydown', this._handleKeydown);
        });
    }

    public animateDivider(show?: boolean) {
        const animationKeyframes = show
            ? StepMarker.animations['divider'].expand
            : StepMarker.animations['divider'].retract;
        const animationOptions = {
            duration: 150,
            iterations: 1,
        };
        const animation = this.divider.animate ? this.divider.animate(animationKeyframes, animationOptions) : undefined;
        if (animation) {
            animation.onfinish = () => {
                if (this.divider) {
                    this.divider.style.width = show ? 100 + '%' : 0 + 'px';
                }
            };
        }
    }

    public grow(show?: boolean) {
        const animationKeyframes = show ? StepMarker.animations['flex'].expand : StepMarker.animations['flex'].retract;
        const animationOptions = {
            duration: 150,
            iterations: 1,
        };
        const showAnimation = this.animate ? this.animate(animationKeyframes, animationOptions) : undefined;
        if (showAnimation) {
            showAnimation.onfinish = () => {
                if (show) this.style.flexGrow = '1';
                else this.style.flexGrow = '0';
            };
        }
    }

    protected firstUpdated(_changedProperties) {
        this._myWidth = this.getBoundingClientRect().width;
        this.setAttribute('mywidth', this._myWidth.toString());

        if (this.type === 'selected') {
            this.animateDivider(true);
            this.grow(true);
            this.requestUpdate();
        }
    }

    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            disabled: this.disabled,
            selected: this.type === 'selected',
            completed: this.completed,
            compact: this.compact,
            hover: this._hoverMouse,
        });

        const numberHtml =
            this.completed && this.type !== 'selected' && !this._hoverMouse
                ? html`<${StepMarker.Icon.tag} icon="${ok}" size="large" class="${baseClassName}-icon"></${StepMarker.Icon.tag}>`
                : this.number;

        return html`
            <div
                class="${rootClasses}"
                @click="${this._handleStepClick}"
                @mouseover="${this._handleMouseOver}"
                @mouseleave="${this._handleMouseLeave}"
                ?disabled="${this.disabled}"
                mywidth="${this._myWidth}"
            >
                <div class="${baseClassName}-mark" tabindex="${this.disabled ? -1 : 1}">${numberHtml}</div>
                <div class="${baseClassName}-slot-wrapper">
                    <slot></slot>
                </div>

                <div class="${baseClassName}-divider"></div>
            </div>
        `;
    }

    private _handleStepClick() {
        if (!this.disabled) {
            this._dispatchEvent(StepMarkerEvent.StepChange);
        }
    }

    private _dispatchEvent(event) {
        this.dispatchEvent(
            new CustomEvent(event, {
                detail: { number: this.number },
                bubbles: true,
                composed: true,
                cancelable: true,
            })
        );
    }
    private _handleKeydown(e) {
        if ((e.code === 'Enter' || e.code === 'Space') && !this.disabled) {
            e.preventDefault();
            this._dispatchEvent(StepMarkerEvent.StepChange);
        }
    }

    private _handleMouseOver() {
        if (this.compact && this.type !== 'selected') {
            this._hoverMouse = true;
            if (this.divider) {
                this.divider.style.width = 0 + 'px';
            }
            this.requestUpdate();
        }
    }

    private _handleMouseLeave() {
        if (this._hoverMouse) {
            this._hoverMouse = false;
            this.requestUpdate();
        }
    }
}
